/* 스타일 리셋 */
@import 'reset.css';
/* 커스텀 디자인 */
:root {
  --color-white : #e6e6e6;
  --color-green : #1d8109;
  --color-yellow : #fffad6;
  --color-grey : #BABCBE;

  --font-family-Noto : 'Noto Sans KR';
  --font-family-Enjoy : 'OTEnjoystoriesBA';
  --font-family-G-Medium : 'GmarketSansMedium';
  --font-family-G-Bold : 'GmarketSansBold';

  --animation-duration : 300ms;
}
/* 폰트 (4종류) */
@font-face {
    font-family: 'OTEnjoystoriesBA';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/OTEnjoystoriesBA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'GmarketSansBold';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansBold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 400;
    src: url("https://fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Regular.woff2") format('woff2'),
    url("https://fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Regular.woff") format('woff'),
    url("https://fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Regular.otf") format('opentype');
}

html {
  font-size : 16px;
}

button, input {
  border : none;
  border-radius: 8px;
  outline : none;
  padding: 8px 12px;
  cursor : pointer;
  margin-left: .5rem;
}

button {
  background-color: var(--color-green);
  color: var(--color-yellow);
  transition: var(--animation-duration) opacity;
}

button:hover {
  opacity: .77;
}

.containers {
  width : 100%;
  height : 100vh;
  background-color : var(--color-yellow);
  font-family : var(--font-family-G-Medium);
}

#navbar {
  width : 100%;
  height : 2.5rem;
  padding : 1.5rem;
  display : flex;
  align-items : center;
  background-color : var(--color-green);
}

.nav-title {
  font-family : var(--font-family-Enjoy);
  font-size : 3rem;
  margin-right : 5rem;
}

.nav-menu > * {
  padding : .5rem 1rem;
  margin : 0 1rem;
  border-bottom : 1px solid transparent;
  transition : var(--animation-duration) all;
}

.nav-menu > *:hover {
  border-bottom : 1px solid var(--color-yellow);
}

#home-container {
  width : 100%;
  height : 100vh;
  display : flex;
  justify-content : center;
  align-items : center;
}
  
.home-btn {
  font-size : 3rem;
  font-family : var(--font-family-G-Bold);
}

#nowplaing-container {
  height : 100%;
}

#nowplaing-container h2 {
  margin : 2rem;
  font-size : 3rem;
  color : var(--color-green);
  font-family : var(--font-family-Enjoy);
  cursor : default;
}

.cards-container {
  display : flex;
  flex-wrap : wrap;
  margin : 2rem;
  height : 75%;
  overflow-y : scroll;
}

.cards {
  width : 240px;
  height : 320px;
  margin : 3rem 1rem;
  opacity : .77;
  transition : var(--animation-duration) all;
}

.cards:hover {
  opacity : 1;
  transform : scale(1.015);
  cursor : pointer;
}

.cards img {
  width : 100%;
  height : 320px;
  border-radius : 8px;
  margin-bottom : 1rem;
}

.cards-description {
  padding : 0 1rem;
  color : var(--color-green);
  display : flex;
  align-items : center;
  justify-content : space-between;
  line-height : 1.5;
}
/* 디테일 */
#detail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}

.detail-wrapper {
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 1200px;
  color : var(--color-green);
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.detail-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.detail-title {
  font-size: 2rem;
  font-weight: bold;
}

.detail-rating {
  font-size: 1.5rem;
}

.detail-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
}

.detail-poster {
  width: 40%;
  height: auto;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.detail-info {
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: 20px;
}

.detail-info > p {
  margin-bottom : 2rem;
}

.detail-overview {
  font-size: 1.2rem;
  margin-bottom: 10px;
  line-height : 1.5;
}

.detail-release,
.detail-runtime {
  font-size: 1.1rem;
  margin-bottom: 5px;
}
